import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { connect } from 'react-redux';
import { signOut as signOutAction } from 'modules/account/actions';
import appRoutes from 'routes/app';
import { Redirect } from 'react-router';
import FormBuilder from 'components/atoms/FormBuilder';
import FormField from 'components/atoms/FormField';
import Block from 'components/atoms/Block';
import Text from 'components/atoms/Text';
import Link from 'components/atoms/Link';
import InputText from 'components/atoms/InputText';
import Message from 'components/atoms/Message';
import { getTranslations } from 'modules/systemData/selectors';
import PasswordScore from 'components/atoms/PasswordScore';

import { onSubmit } from './store/actions';
import styles from './UserInvitedForm.scss';

class UserInvitedForm extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      redirectToLogin: false,
    };
  }

  componentWillReceiveProps(nextProps) {
    if (!nextProps.submitting && nextProps.submitSucceeded) {
      this.props.signOut();
      this.setState({ redirectToLogin: true });
    }
  }

  render() {
    const { submitting, submitSucceeded, error, formValues: { password }, translations, company } = this.props;

    return (
      <Block className={styles.wrapper}>
        {this.state.redirectToLogin && <Redirect to={appRoutes.account.signIn} />}
        <Block className="fields-full">
          <FormField name="email" id="email" component={InputText} type="email" placeholder="Email address" disabled />
        </Block>

        <Block className="fields-full">
          <FormField name="fullName" id="fullName" component={InputText} type="text" placeholder="Full name" />
        </Block>

        <Block className={classNames('fields-full', styles.passwordField)}>
          <FormField
            name="password"
            id="password"
            component={InputText}
            type="password"
            placeholder={translations.placeholderPassword}
            autoComplete="off"
          />
          <Block className="pass-wrapper">
            <Block className="pass-graybar">
              <Block className="pass-colorbar" style={{ backgroundPosition: '0px -46px', width: '46%' }} />
            </Block>
            <span className="pass-text">
              <PasswordScore password={password || ''} />
            </span>
          </Block>
        </Block>

        {!submitting &&
          submitSucceeded && (
            <Block className="fields-full fields-padd">
              <Message type="success" size="big">
                {translations.genericFormSuccess}
              </Message>
            </Block>
          )}
        {!submitting &&
          error && (
            <Block className="fields-full fields-padd">
              <Message type="alert" size="big">
                {error}
              </Message>
            </Block>
          )}

        <Block className="fields-full fields-padd">
          <button type="submit" className="button-blue">
            {translations.translate('companyJoinCompanyName', { company })}
          </button>
        </Block>
        <Block className="fields-full form-links-bottom flexcenter">
          <Link href="#" className="simplelink-normal">
            <Text>{translations.genericSignInWithGoogle}</Text>
          </Link>
        </Block>
      </Block>
    );
  }
}

UserInvitedForm.propTypes = {
  error: PropTypes.string,
  submitting: PropTypes.bool,
  submitSucceeded: PropTypes.bool,
  // eslint-disable-next-line react/no-unused-prop-types
  company: PropTypes.string.isRequired,
  // eslint-disable-next-line react/no-unused-prop-types
  email: PropTypes.string.isRequired,
  // eslint-disable-next-line react/no-unused-prop-types
  inviteId: PropTypes.string.isRequired,
  // eslint-disable-next-line react/no-unused-prop-types
  // eslint-disable-next-line react/no-unused-prop-types
  valid: PropTypes.bool,
  formValues: PropTypes.shape({
    password: PropTypes.string,
    email: PropTypes.string,
  }),
  translations: PropTypes.object.isRequired,
  signOut: PropTypes.func,
};

UserInvitedForm.defaultProps = {
  error: null,
  submitting: null,
  submitSucceeded: null,
  valid: false,
  formValues: {},
  signOut: null,
};

const mapStateToProps = state => ({
  translations: getTranslations(state),
});

const mapDispatchToProps = {
  signOut: signOutAction,
};

export default connect(mapStateToProps, mapDispatchToProps)(
  FormBuilder(props => {
    const initialValues = {
      email: props.email,
      invitationToken: props.invitationToken,
      inviteId: props.inviteId,
    };
    return {
      form: 'UserInvitedForm',
      className: styles.userInvitedForm,
      onSubmit,
      initialValues,
      validate: {
        fullName: {
          required: true,
        },
        password: {
          rules: {
            required: true,
            minLength: 6,
          },
          messages: {
            minLength: props.translations.messagePasswordTooShort,
          },
        },
        email: {
          rules: {
            required: true,
            format: 'email',
          },
          messages: {
            format: props.translations.messageInvalidEmail,
          },
        },
      },
    };
  })(UserInvitedForm),
);



