import React, { Component } from 'react';
import classNames from 'classnames';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Block from 'components/atoms/Block';
import Image from 'components/atoms/Image';
import Text from 'components/atoms/Text';
import Link from 'components/atoms/Link';
import AllRightsReservedBlock from 'components/atoms/AllRightsReservedBlock';
import Section from 'components/atoms/Section';
import Paragraph from 'components/atoms/Paragraph';
import { registerInvitedUser, signOut as signOutAction } from 'modules/account/actions';
import UserInvitedForm from 'components/organismes/UserInvitedForm';
import { getTranslations } from 'modules/systemData/selectors';
import appRoutes from 'routes/app';
import logo from 'images/logo.png';
import getInvitedUserInfo from './store/actions';

import styles from './UserInvitedPage.scss';

// eslint-disable-next-line react/prefer-stateless-function
class UserInvitedPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      company: '',
      requestedBy: '',
      email: '',
      inviteId: '',
      token: '',
      showSuccessMessage: false,
      error: null,
      userNotFound: false,
    };
  }
  componentWillMount = async () => {
    const { match: { params: { inviteId, token } = {} } } = this.props;

    const response = await this.props.getInvitedUserInfo({
      inviteId,
      token,
    });

    if (response.email) {
      this.setState({ company: response.company, requestedBy: response.requestedBy });
      if (response.userExists) {
        const result = await this.props.registerInvitedUser({ inviteId, invitationToken: token });
        if (result.status === 'error') {
          this.setState({ error: result });
        } else {
          this.props.signOut();
          this.setState({ showSuccessMessage: true });
        }

        setTimeout(() => {
          this.props.history.push(appRoutes.account.signIn);
        }, 4000);
      } else {
        this.setState({ ...response, inviteId, token, userNotFound: true });
      }
    } else {
      this.props.history.push(appRoutes.account.signIn);
    }
  };

  render() {
    const { requestedBy, company, email, inviteId, token, showSuccessMessage, userNotFound, error } = this.state;

    const { translations } = this.props;

    return (
      <Section className="sign signin">
        <Block className="side-form">
          <Block className="head">
            <Link href="/">
              <Image src={logo} alt="Hutwork" />
            </Link>
          </Block>
          <Block className="section-from">
            <Block className="container-sign">
              <Block className="top">
                <h1>
                  {showSuccessMessage ? translations.genericJoined : translations.genericJoin} {company}
                </h1>
                {showSuccessMessage ? (
                  <Paragraph>
                    <Text className={classNames(styles.success)}>
                      {translations.translate('companyExistingUserAddSuccess', { companyKey: company })}
                    </Text>
                  </Paragraph>
                ) : (
                  <Paragraph>
                    {translations.translate('companyInvitedToJoin', { requestedBy, company })}{' '}
                    {userNotFound && translations.companyPleaseFillOut}
                  </Paragraph>
                )}
                {error && (
                  <Paragraph>
                    <Text className={classNames(styles.error)}>{error.message}</Text>
                  </Paragraph>
                )}
              </Block>
              <Block className="body">
                {/* Form */}
                <Block className="formular">
                  {email && (
                    <UserInvitedForm company={company} email={email} inviteId={inviteId} invitationToken={token} />
                  )}
                </Block>
                {/* END Form */}
              </Block>
            </Block>
          </Block>
          <Block className="bot">
            <AllRightsReservedBlock />
          </Block>
        </Block>
        <Block className={classNames(styles.sideImage)} />
      </Section>
    );
  }
}

UserInvitedPage.propTypes = {
  // eslint-disable-next-line react/forbid-prop-types
  history: PropTypes.object,
  match: PropTypes.shape({
    params: PropTypes.shape({
      userId: PropTypes.string,
      token: PropTypes.string,
    }),
  }),
  getInvitedUserInfo: PropTypes.func.isRequired,
  registerInvitedUser: PropTypes.func.isRequired,
  translations: PropTypes.object.isRequired,
  signOut: PropTypes.func,
};

UserInvitedPage.defaultProps = {
  history: {},
  match: {},
  signOut: null,
};

const mapStateToProps = state => ({
  translations: getTranslations(state),
});

const mapDispatchToProps = {
  getInvitedUserInfo,
  registerInvitedUser,
  signOut: signOutAction,
};

export default connect(mapStateToProps, mapDispatchToProps)(UserInvitedPage);



